<template><section><h1>Button</h1>
<template>
    <coding :code="type" title="三种样式" content="button有三种样式:primary,ghost,dashed" lang="html">
        <y-button type="primary">一个按钮</y-button>
        <y-button type="ghost">一个按钮</y-button>
        <y-button type="dashed">一个按钮</y-button>
    </coding>
    <coding :code="state" title="两种状态" content="可以添加disabled和loading选择按钮状态" lang="html">
        <y-button type="primary" disabled>一个按钮</y-button>
        <y-button type="primary" loading="">一个按钮</y-button>
    </coding>
    <coding :code="on" title="原生事件" content="可以给按钮添加原生事件。" lang="html">
        <y-button type="ghost" @click.native="test">一个按钮</y-button>
    </coding>
    <coding :code="color" title="颜色" content="有四种颜色可选，默认蓝色。" lang="html">
        <y-button type="primary">一个按钮</y-button>
        <y-button type="ghost">一个按钮</y-button>
        <y-button type="dashed">一个按钮</y-button>
        <br>
        <br>
        <y-button type="primary" color="green">一个按钮</y-button>
        <y-button type="ghost" color="green">一个按钮</y-button>
        <y-button type="dashed" color="green">一个按钮</y-button>
        <br>
        <br>
        <y-button type="primary" color="red">一个按钮</y-button>
        <y-button type="ghost" color="red">一个按钮</y-button>
        <y-button type="dashed" color="red">一个按钮</y-button>
        <br>
        <br>
        <y-button type="primary" color="yellow">一个按钮</y-button>
        <y-button type="ghost" color="yellow">一个按钮</y-button>
        <y-button type="dashed" color="yellow">一个按钮</y-button>
    </coding>
</template>

<h2>参数设置</h2>
<table>
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
<th>可选值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>按钮的类型</td>
<td><code>String</code></td>
<td><code>primary</code></td>
<td><code>ghosts</code>,<code>dashed</code></td>
</tr>
<tr>
<td>size</td>
<td>按钮的大小</td>
<td><code>String</code></td>
<td>-</td>
<td><code>lg</code>,<code>sm</code></td>
</tr>
<tr>
<td>disabled</td>
<td>可选状态</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>loading</td>
<td>加载</td>
<td><code>Boolean</code></td>
<td><code>false</code></td>
<td>-</td>
</tr>
<tr>
<td>color</td>
<td>按钮的颜色</td>
<td><code>String</code></td>
<td><code>blue</code></td>
<td><code>green</code>,<code>yellow</code>,<code>red</code>,<code>gray</code></td>
</tr>
</tbody>
</table>
</section></template>

<script>
let type =
`<template>
    <y-button type="primary">一个按钮</y-button>
    <y-button type="ghost">一个按钮</y-button>
    <y-button type="dashed">一个按钮</y-button>
</template>
`
let state =
`<template>
    <y-button type="primary" disabled>一个按钮</y-button>
    <y-button type="primary" loading>一个按钮</y-button>
</template>
`
let on =
`<template>
    <y-button type="ghost" @click.native="test">一个按钮</y-button>
</template>
`
let color =
`<template>
    <y-button type="primary">一个按钮</y-button>
    <y-button type="ghost">一个按钮</y-button>
    <y-button type="dashed">一个按钮</y-button>
    <y-button type="primary" color="green">一个按钮</y-button>
    <y-button type="ghost" color="green">一个按钮</y-button>
    <y-button type="dashed" color="green">一个按钮</y-button>
    <y-button type="primary" color="red">一个按钮</y-button>
    <y-button type="ghost" color="red">一个按钮</y-button>
    <y-button type="dashed" color="red">一个按钮</y-button>
    <y-button type="primary" color="yellow">一个按钮</y-button>
    <y-button type="ghost" color="yellow">一个按钮</y-button>
    <y-button type="dashed" color="yellow">一个按钮</y-button>
</template>
`
export default {
    data(){
        return {
            type:type,
            state:state,
            on:on,
            color:color,
        }
    },
    methods:{
        test(){
            this.$notify({"title":"test","content":"sfafasdf","time":1000,"icon":true})
        }
    }
}
</script>


